<!--  -->
<template>
  <div class="bookmark" ref="bookmark" :style="style"></div>
</template>

<script>
import { px2rem } from '../../utils/utils'
export default {
  props: {
    color: String,
    width: Number,
    height: Number,
  },
  computed: {
    style() {
      if (this.color) {
        // console.log(this.color)
        return {
          borderColor: `${this.color} ${this.color} transparent ${this.color}`,
        }
      } else {
        return {}
      }
    },
  },
  methods: {
    refresh() {
      if (this.width && this.height) {
        this.$refs.bookmark.style.borderWidth = `${px2rem(
          this.height - 5
        )}rem ${px2rem(this.width / 2)}rem ${px2rem(10)}rem ${px2rem(
          this.width / 2
        )}rem`
      }
    },
  },
  data() {
    return {}
  },
  mounted() {
    this.refresh()
  },
}
</script>
<style lang="scss" scoped>
@import '../../assets/styles/global';
.bookmark {
  width: 0;
  height: 0;
  border-width: px2rem(50) px2rem(10) px2rem(10) px2rem(10);
  border-color: red red transparent red;
  border-style: solid;
}
</style>
