<!--  -->
<template>
  <transition name="popup-slide-up">
    <div class="ebook-popup-list" v-show="fontFamilyVisible">
      <div class="popup-title-wrapper">
        <div class="popup-icon-wrapper" @click="hiddenFontFimaly">
          <span class="icon-down2"></span>
        </div>
        <span class="popup-title">{{ $t('book.selectFont') }}</span>
      </div>
      <div class="ebook-popup-list-wrapper">
        <div
          class="ebook-popup-item"
          v-for="(item, index) in fontFamilyList"
          :key="index"
          @click="setFontFamily(item.font)"
        >
          <div
            class="ebook-popup-item-text"
            :class="{ selected: isSelected(item) }"
          >
            {{ item.font }}
          </div>
          <div
            class="ebook-popup-item-check"
            :class="{ selected: isSelected(item) }"
          >
            <span class="icon-check"></span>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { ebookMixin } from '../../utils/mixin'
import { FONT_FAMILY } from '../../utils/book'
import { setFontFamily } from '../../utils/localStorage'
export default {
  mixins: [ebookMixin],
  mounted() {
    // console.log(this.$t)
  },
  methods: {
    isSelected(item) {
      return item.font === this.defaultFontFamily
    },
    setFontFamily(font) {
      this.setDefaultFontFamily(font)

      if (font === 'Default') {
        this.currentBook.rendition.themes.font('Times New Roman')
        setFontFamily(this.fileName, 'Times New Roman')
      } else {
        this.currentBook.rendition.themes.font(font)
        setFontFamily(this.fileName, font)
      }

      // console.log(font)
    },
    hiddenFontFimaly() {
      this.setFontFamilyVisible(false)
    },
  },
  data() {
    return {
      fontFamilyList: FONT_FAMILY,
    }
  },
}
</script>
