<!--  -->
<template>
  <div class="ebook-bookmark" ref="bookmark">
    <div class="ebook-bookmark-text-wrapper">
      <div class="ebook-bookmark-down-wrapper" ref="iconDown">
        <span class="icon-down"></span>
      </div>
      <div class="ebook-bookmark-text">{{ text }}</div>
    </div>
    <div class="ebook-bookmark-icon-wrapper" :style="isFixed ? fixedStyle : {}">
      <Bookmark :color="color" :height="35" :width="15" />
    </div>
  </div>
</template>

<script>
import Bookmark from '../../components/common/Bookmark'
import { realPx } from '../../utils/utils'
import { ebookMixin } from '../../utils/mixin'
import { getBookMark, setBookMark } from '../../utils/localStorage'
const BLUE = '#346cbc'
const WHITE = '#777'
export default {
  mixins: [ebookMixin],
  components: {
    Bookmark,
  },
  computed: {
    height() {
      return realPx(35)
    },
    threshold() {
      return realPx(55)
    },
    fixedStyle() {
      return {
        position: 'fixed',
        top: 0,
        right: `${(window.innerWidth - this.$refs.bookmark.clientWidth) / 2}px`,
      }
    },
  },
  watch: {
    offsetY(v) {
      if (!this.bookAvailable || this.menuVisible || this.settingVisible > 0) {
        return
      }
      // console.log(v)
      if (v >= this.height && v < this.threshold) {
        //
        // console.log('')
        this.beforeThreshold(v)
      } else if (v >= this.threshold) {
        //
        // this.$refs.bookmark.style.top = px2rem(-35)
        this.afterThreshold(v)
        // console.log('')
      } else if (v > 0 && v < this.height) {
        //1
        this.beforeHeight()
      } else if (v === 0) {
        this.restore()
      }
    },
    isBookmark(isBookmark) {
      this.isFixed = isBookmark
      if (isBookmark) {
        this.color = BLUE
      } else {
        this.color = WHITE
      }
    },
  },
  data() {
    return {
      color: WHITE,
      text: '',
      isFixed: false,
    }
  },
  methods: {
    addBookMark() {
      this.bookmark = getBookMark(this.fileName)
      if (!this.bookmark) {
        this.bookmark = []
      }
      const currentLocation = this.currentBook.rendition.currentLocation()
      // console.log(currentLocation)
      const cfibase = currentLocation.start.cfi.replace(/!.*/, '')
      const cfistart = currentLocation.start.cfi
        .replace(/.*!/, '')
        .replace(/\)$/, '')
      const sfiend = currentLocation.end.cfi
        .replace(/.*!/, '')
        .replace(/\)$/, '')
      const cfirange = `${cfibase}!,${cfistart},${sfiend})`
      // console.log(cfirange)
      // console.log(this.currentBook.getRange(cfirange))
      this.currentBook.getRange(cfirange).then((range) => {
        const text = range.toString().replace(/\s\s/g, '')
        this.bookmark.push({ cfi: currentLocation.start.cfi, text: text })
        setBookMark(this.fileName, this.bookmark)
      })
      // console.log(currentLocation)
    },
    removeBookMark() {
      const currentLocation = this.currentBook.rendition.currentLocation()
      const currentCfi = currentLocation.start.cfi
      this.bookmark = getBookMark(this.fileName)
      if (this.bookmark) {
        const res = this.bookmark.filter((item) => {
          // console.log(item.cfi === currentCfi)
          return item.cfi !== currentCfi
        })
        setBookMark(this.fileName, res)
        this.setIsBookmark(false)
      }
    },
    restore() {
      setTimeout(() => {
        this.$refs.bookmark.style.top = `${-this.height}px`
        this.$refs.iconDown.style.transform = 'rotate(0deg)'
      }, 200)
      if (this.isFixed) {
        this.setIsBookmark(true)
        this.addBookMark()
      } else {
        this.setIsBookmark(false)
        this.removeBookMark()
      }
    },
    beforeHeight() {
      if (this.isBookmark) {
        this.text = this.$t('book.pulldownDeleteMark')
        this.color = BLUE
        this.isFixed = true
      } else {
        this.text = this.$t('book.pulldownAddMark')
        this.color = WHITE
        this.isFixed = false
      }
    },
    beforeThreshold(v) {
      this.$refs.bookmark.style.top = `${-v}px`
      if (this.isBookmark) {
        this.text = this.$t('book.pulldownDeleteMark')
        this.color = BLUE
      } else {
        this.text = this.$t('book.pulldownAddMark')
        this.color = WHITE
      }

      const iconDown = this.$refs.iconDown
      if (iconDown.style.transform === 'rotate(180deg)') {
        iconDown.style.transform = 'rotate(0deg)'
      }
    },
    afterThreshold(v) {
      this.$refs.bookmark.style.top = `${-v}px`
      if (this.isBookmark) {
        this.text = this.$t('book.releaseDeleteMark')
        this.color = WHITE
        this.isFixed = false
      } else {
        this.text = this.$t('book.releaseAddMark')
        this.color = BLUE
        this.isFixed = true
      }

      const iconDown = this.$refs.iconDown
      if (
        iconDown.style.transform === 'rotate(0deg)' ||
        iconDown.style.transform === ''
      ) {
        iconDown.style.transform = 'rotate(180deg)'
      }
    },
  },
}
</script>
<style lang="scss" scoped>
@import '../../assets/styles/global';
.ebook-bookmark {
  position: absolute;
  top: px2rem(-35);
  left: 0;
  z-index: 200;
  // transform: rotate(180deg);
  width: 100%;
  height: px2rem(35);
  .ebook-bookmark-text-wrapper {
    position: absolute;
    right: px2rem(45);
    bottom: 0;
    display: flex;
    .ebook-bookmark-down-wrapper {
      font-size: px2rem(14);
      // transform: rotate(0deg);
      // color: white;
      transition: all 0.2s linear;
      @include center;
    }
    .ebook-bookmark-text {
      font-size: px2rem(14);
      // color: white;
    }
  }
  .ebook-bookmark-icon-wrapper {
    position: absolute;
    right: 0;
    bottom: 0;
    margin-right: px2rem(15);
  }
}
</style>
